import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
		};
	},
	onLoad(){
		// this.loadData();
	},
	onShow() {
		this.loadData();
	},
	onHide() {
		// this.cartList = [];
	},
	watch:{
		//显示空白页
		cartList(e){
			let empty = e.length === 0 ? true: false;
			if(this.empty !== empty){
				this.empty = empty;
			}
		}
	},
	computed:{
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData(){
			// let list = await this.$api.json('cartList'); 
			let { data,code } = await this.Api.getCartList();
			if(code == 1) {
				this.cartList = data;
				this.calcTotal();  //计算总价
			}
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		 //选中状态处理
		async check(type, index){
			
			if(type === 'item'){
				let params = {
					checked: !this.cartList[index].checked,
					_id: this.cartList[index]._id
				}
				await this.Api.editCart(params);
				this.cartList[index].checked = !this.cartList[index].checked;
			}else{
				const checked = !this.allChecked;
				let params = {
					allChecked: checked,
				}
				await this.Api.editCart(params);
				const list = this.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		async numberChange(data){
			let params = {
				number: data.number,
				_id: this.cartList[data.index]._id
			}
			let data2 = await this.Api.editCart(params);
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index,id){
			let id2 = id;
			uni.showModal({
				content: '删除当前商品？',
				success: (e)=>{
					if(e.confirm){
						let params = {
							_id: id2
						}
						this.Api.deleteCart(params);
						let list = this.cartList;
						let row = list[index];
						let id = row.id;
						this.cartList.splice(index, 1);
						this.calcTotal();
						uni.hideLoading();
					}
				}
			})
		},
		//清空
		clearCart(){
			uni.showModal({
				content: '清空购物车？',
				success: (e)=>{
					if(e.confirm){
						let params = {
							allDelete: true
						}
						this.Api.deleteCart(params);
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal(){
			let list = this.cartList;
			if(list.length === 0){
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item=>{
				if(item.checked === true){
					total += item.price * item.number;
				}else if(checked === true){
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createOrder(){
			if(this.total == 0) {
				this.$api.msg('请选择商品！');
			} else {
				uni.navigateTo({
					url: '/pages/order/createOrder'
				})
			}
		}
	}
}
